export default {
	data() {
		return {

		};
	},
	methods: {
		goto(e) {
			if(this.$common.userinfo.id){
				var url = this.cardslist[e].url
			}else{
				var url = '../loginPage/loginPage'
			}
			uni.navigateTo({
				url: url
			})
		}
	},
